<!-- Test.svelte -->
<script>
  let selectedTab = ''; // Default selected tab
  let currentQuestionIndex = 0; // Index of the current question
  let userAnswers = []; // User's answers
  let showScore = false; // To show score at the end
  let ready = false; // To show score at the end
  let score = 0; // User's score

  // Sample question structure
  const basicQuestions = [
    
{ question: "Co to jest 'globalne ocieplenie'?", answers: ["a) Zjawisko zwizane z zimowym ubraniem", "b) Stopniowe podwyszanie si redniej temperatury na Ziemi", "c) Nazwa jednej z planet w Ukadzie Sonecznym", "d) Metoda uprawy rolin w szklarniach"], correctAnswer: "b) Stopniowe podwyszanie si redniej temperatury na Ziemi" },

{ question: "Jaka jest gwna przyczyna topnienia lodowcw na wiecie?", answers: ["a) Nadmierna ilo deszczu", "b) Emisja gazw cieplarnianych", "c) Wprowadzanie chemikaliw do wd oceanicznych", "d) Wpyw dziaalnoci wulkanicznej"], correctAnswer: "b) Emisja gazw cieplarnianych" },

{ question: "Skd pochodzi wikszo energii elektrycznej w Polsce?", answers: ["a) Wiatr", "b) Wgiel", "c) Soce", "d) Gaz ziemny"], correctAnswer: "b) Wgiel" },

{ question: "Co to jest 'recykling'?", answers: ["a) Proces oczyszczania rzek", "b) Ponowne wykorzystywanie surowcw z odpadw", "c) Metoda sadzenia drzew w lasach", "d) Oznaczanie obszarw chronionych"], correctAnswer: "b) Ponowne wykorzystywanie surowcw z odpadw" },

{ question: "Ktre zjawisko atmosferyczne jest zwizane z nadmiernymi opadami deszczu, powodujcymi zalania?", answers: ["a) Tornado", "b) Susza", "c) Gradobicie", "d) Powd"], correctAnswer: "d) Powd" },

{ question: "Dlaczego topnienie lodowcw ma wpyw na poziom mrz i oceanw?", answers: ["a) Zmniejsza ilo wody pitnej na Ziemi", "b) Zwiksza objto wd oceanicznych", "c) Powoduje przemieszczanie si kontynentw", "d) Sprzyja rozwojowi raf koralowych"], correctAnswer: "b) Zwiksza objto wd oceanicznych" },

{ question: "Ktre z miast w Polsce jest najbardziej zanieczyszczone?", answers: ["a) Katowice", "b) Nowa Ruda", "c) Krakw", "d) Warszawa"], correctAnswer: "b) Nowa Ruda" },

{ question: "Czym s opady kwasowe i dlaczego s szkodliwe dla rodowiska?", answers: ["a) Deszcze zawierajce kwasy, szkodliwe dla gleb", "b) Opady atmosferyczne z kwasami, szkodliwe dla rzek", "c) Opady deszczu zawierajce substancje kwasowe, szkodliwe dla rolin i wd", "d) Kwane deszcze, korzystne dla rolnictwa"], correctAnswer: "c) Opady deszczu zawierajce substancje kwasowe, szkodliwe dla rolin i wd" },

{ question: "Co to jest 'efekt cieplarniany'?", answers: ["a) Zjawisko ogrzewania klimatu przez soce", "b) Wzrost temperatury w kabinie sauny", "c) Zatrzymywanie ciepa przez atmosfer Ziemi", "d) Nazwa jednej z planet w Ukadzie Sonecznym"], correctAnswer: "c) Zatrzymywanie ciepa przez atmosfer Ziemi" },

{ question: "Dlaczego ochrona biornorodnoci jest istotna dla ekosystemw?", answers: ["a) Zwiksza ilo pestycydw w glebie", "b) Chroni przed poarami lasw", "c) Zapewnia stabilno ekosystemw, utrzymanie rwnowagi biologicznej", "d) Sprzyja szybszemu wzrostowi populacji gatunkw zagroonych"], correctAnswer: "c) Zapewnia stabilno ekosystemw, utrzymanie rwnowagi biologicznej" },
    // ... More questions
  ];

  const passionateQuestions = [
    { question: "Jakie konkretnie gazy cieplarniane przyczyniaj si do wzrostu efektu cieplarnianego?", answers: ["a) Metan, dwutlenek wgla, podtlenek azotu", "b) Ozon, argon, ksenon", "c) Azot, tlen, dwutlenek siarki", "d) Propan, butan, etanol"], correctAnswer: "a) Metan, dwutlenek wgla, podtlenek azotu" },

    { question: "Jakie zjawisko atmosferyczne moe by skutkiem globalnych zmian klimatu?", answers: ["a) Trba powietrzna", "b) Gradobicie", "c) El Nio", "d) Mga radiacyjna"], correctAnswer: "c) El Nio" },

    { question: "W jaki sposb dziaalno czowieka wpywa na zjawisko suszy?", answers: ["a) Zwikszajc ilo opadw deszczu", "b) Zanieczyszczajc wody gruntowe", "c) Emitujc gazw cieplarnianych", "d) Wycinajc lasy"], correctAnswer: "d) Wycinajc lasy" },

    { question: "Co to jest 'kwas deszczowy' i jakie ma skutki?", answers: ["a) Deszcz zawierajcy kwasy, korzystny dla rolnictwa", "b) Opady deszczu o niskim pH, szkodliwe dla gleb i wd", "c) Deszcz pochodzcy z obszarw przemysowych, bogaty w mineray", "d) Deszcz o silnym nateniu, powodujcy erozj gleby"], correctAnswer: "b) Opady deszczu o niskim pH, szkodliwe dla gleb i wd" },

    { question: "Dlaczego rozwj rolnictwa ekologicznego jest uwaany za korzystny dla rodowiska?", answers: ["a) Wiksza ilo stosowanych pestycydw", "b) Mniejsze zuycie wody", "c) Intensywniejsze stosowanie sztucznych nawozw", "d) Zwikszona emisja gazw cieplarnianych"], correctAnswer: "b) Mniejsze zuycie wody" },

    { question: "Co to jest 'krajobraz kulturowy' i dlaczego jest wany dla ochrony rodowiska?", answers: ["a) Obszar geograficzny o niekorzystnych warunkach atmosferycznych", "b) Formacja geologiczna charakteryzujca si unikalnymi cechami", "c) Obszar, na ktrym ludzie ksztatowali rodowisko przez wieki", "d) Specjalnie chroniony obszar przyrodniczy"], correctAnswer: "c) Obszar, na ktrym ludzie ksztatowali rodowisko przez wieki" },

    { question: "Ktre z poniszych zwierzt jest zagroone wyginiciem w Polsce z powodu utraty naturalnych siedlisk?", answers: ["a) ubr", "b) Ry", "c) Kuna lena", "d) Sarna"], correctAnswer: "b) Ry" },

    { question: "W wyniku jakiego procesu nastpuje zakwaszanie wd powierzchniowych, co wpywa na ycie organizmw wodnych?", answers: ["a) Emisja CO2", "b) Rozpuszczanie siarki", "c) Wydobycie ropy naftowej", "d) Opady kwasowe"], correctAnswer: "d) Opady kwasowe" },

    { question: "Ktre z poniszych przedsiwzi promuje ochron przyrody i biornorodnoci w Polsce?", answers: ["a) Wylesianie", "b) Budowa autostrad", "c) Program Natura 2000", "d) Rozwj kopalni"], correctAnswer: "c) Program Natura 2000" },

    { question: "Jakie s gwne skutki zanieczyszczenia wd powierzchniowych dla ycia organizmw wodnych?", answers: ["a) Zwikszenie iloci tlenu w wodzie", "b) Zagroenie dla organizmw wodnych przez substancje chemiczne", "c) Poprawa jakoci wd", "d) Zwikszenie biornorodnoci"], correctAnswer: "b) Zagroenie dla organizmw wodnych przez substancje chemiczne" },

    // ... More questions
  ];

  function selectTab(tab) {
    ready = true;
    selectedTab = tab;
    currentQuestionIndex = 0;
    userAnswers = [];
    showScore = false;
    score = 0;
  }

  function selectAnswer(answer) {
    userAnswers[currentQuestionIndex] = answer;
    if (currentQuestionIndex < getQuestions().length - 1) {
      currentQuestionIndex++;
    } else {
      calculateScore();
      showScore = true;
    }
  }

  function calculateScore() {
    let correctAnswers = getQuestions().map(q => q.correctAnswer);
    score = userAnswers.reduce((acc, answer, index) => acc + (answer === correctAnswers[index] ? 1 : 0), 0);
  }

  function getQuestions() {
    return selectedTab === 'basic' ? basicQuestions : passionateQuestions;
  }
</script>

<style>
  .test-container {
    text-align: center;
    padding: 20px;
  }

  .tab {
    display: inline-block;
    background-color: #00895c;
    padding: 10px 20px;
    margin: 0 10px;
    color: white;
    outline: none;
    border-radius: 15px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
    transition: 0.3s;
  }

  .tab:focus {
    box-shadow: 0 0 0 2px white, 0 0 0 4px #66db6a;
  }

  .tab:hover {
    box-shadow: 0 16px 32px rgba(0,0,0,0.2);
  }

  .active {
    background-color: #2e9d32;
  }

  .tab-content {
    margin-top: 20px;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    font-weight: bolder;
  }

  p {
    color: black;
  }

  #holder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%; /* Ensure it takes the full width */
  }

  #score {
    color: white;
  }

  .answer-btn {
    width: 100%;
    display: block; /* Makes button take the full width */
    margin: 10px auto; /* Centers button and adds spacing */
    padding: 10px 20px; /* Adjust as needed for padding */
    background-color: #00895c; /* Background color for the button */
    color: rgb(255, 255, 255); /* Text color */
    border: 1px solid #000000; /* Border for the button */
    border-radius: 4px; /* Rounded corners */
    cursor: pointer; /* Cursor changes to pointer to indicate it's clickable */
    transition: background-color 0.3s; /* Smooth transition for hover effect */
    font-weight: bolder;
  }

  .answer-btn:hover {
    background-color: #66db6a; /* Slightly darker background on hover */
  }
</style>

<div class="test-container">
  {#if !ready}
  <button class="tab {selectedTab === 'basic' ? 'active' : ''}" on:click={() => selectTab('basic')}>Podstawowy</button>
  <button class="tab {selectedTab === 'passionate' ? 'active' : ''}" on:click={() => selectTab('passionate')}>Pasjonat</button>
  {/if}

  {#if ready}
    {#if !showScore}
      <div class="tab-content" id="holder">
        <p>{getQuestions()[currentQuestionIndex].question}</p>
        {#each getQuestions()[currentQuestionIndex].answers as answer}
          <button class="answer-btn" on:click={() => selectAnswer(answer)}>{answer}</button>
        {/each}
      </div>
    {:else}
      <div class="score-display">
        <p id="score">Twoj wynik: {score}/{getQuestions().length}</p>
      </div>
    {/if}
  {/if}
</div>
